import React, { Component } from "react";
import axios from "axios";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { Route } from "react-router-dom";
import "./App.scss";
import logo from "./assets/images/logo.svg";
import cart from "./assets/images/icons/cart.svg";
import avatar from "./assets/images/icons/avatar.svg";

import Catalog from "./pages/Catalog/view";
import Purchase from "./pages/Purchase/view";
import Landing from "./pages/Landing/view";

class App extends Component {
  constructor(props) {
    super(props);
    console.log("props", this.props);
    this.state = { showSignModal: false };
  }

  async login() {
    await axios.post(
      `http://192.168.0.113:8000/api/v0/auth/login/`,
      { username: "77774158157", password: "dbz4Gb1b" },
      { headers: { "Content-Type": "application/json" } }
    );
  }

  async register() {
    await axios.post(
      `http://192.168.0.113:8000/api/v0/registration/via_sms/`,
      { name: "anchik", phone_number: "77751986202" },
      { headers: { "Content-Type": "application/json" } }
    );
  }

  toggleSignModal() {
    this.setState(prevState => ({ showSignModal: !prevState.showSignModal }));
  }

  render() {
    return (
      <div className="App">
        {this.state.showSignModal ? (
          <div className="sign-popup__wrapper">
            <div className="sign-popup">
              <div>
                <p>Вход</p>
                <input
                  placeholder="Телефон"
                  type="text"
                  className="input mt-3"
                />
                <input
                  placeholder="Пароль"
                  type="password"
                  className="input mt-3"
                />
                <button onClick={() => this.login()}>Войти</button>
              </div>
              <div>
                <p>Регистрация</p>
                <input placeholder="Имя" type="text" className="input mt-3" />
                <input
                  placeholder="Телефон"
                  type="text"
                  className="input mt-3"
                />
                <button onClick={() => this.register()}>Зарегистрироваться</button>
              </div>
            </div>
          </div>
        ) : null}
        <header className="header">
          <div className="container h-100 d-flex align-items-center justify-content-between">
            <Link to="/" className="header-logo">
              <img className="logo__img" src={logo} alt="logo" />
            </Link>
            <div className="header__search">
              <input
                className="input input_search"
                type="text"
                placeholder="Что ищете"
              />
            </div>
            <ul className="nav d-flex align-items-center">
              <li>
                <Link className="nav__link" to="/purchase">
                  Покупка товара
                </Link>
              </li>
              <li>
                <a className="nav__link" href="">
                  О компании
                </a>
              </li>
              <li>
                <a className="nav__link" href="">
                  Контакты
                </a>
              </li>
              <li className="mx-3" />
              <li>
                <Link to="/purchase/cart" className="nav__link" href="">
                  <img className="nav__icon" src={cart} alt="" />
                </Link>
              </li>
              <li>
                <a className="nav__link" onClick={() => this.toggleSignModal()}>
                  <img className="nav__icon" src={avatar} alt="" />
                </a>
              </li>
            </ul>
          </div>
        </header>

        <div className="main-content">
            <Route path={`/`} exact component={Landing} />
            <Route path={`/catalog`} component={Catalog} />
            <Route path={`/purchase`} component={Purchase} />
        </div>

        <div className="bg-cover footer-bg" />
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    state
  };
}

export default connect(mapStateToProps)(App);
